/* Reset & Body */
body {
    margin: 0;
    background-color: #E3E2DF; /* Soft pastel */
    color: #2d3748;
    font-family: 'Segoe UI', sans-serif;
  }
  
  /* Hero Section Styling */
  .hero-section {
    background: linear-gradient(135deg, #ff7eb9, #feb2a6);
    /* background-color: #78589e; */
     /* Soft pink to peach */
    color: white;
    padding: 30px 30;
    font-family: 'Roboto', sans-serif;
  }
  
  .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 40px 20px; /* ⬅️ 40px top/bottom, 20px left/right */
  }
  
  
  /* Badge / Tagline */
  .badge {
    background-color: #ffffff;
    color: #124E66;
    display: inline-block;
    padding: 10px 0px;
    border-radius: 20px;
    margin-bottom: 20px;
    font-size: 1.2rem;
    font-weight: bold;
  }
  
  /* Hero Grid */
  .hero-grid {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 40px;
    padding: 60px 0;
  }
  
  .hero-content-left {
    flex: 1;
    min-width: 300px;
  }
  
  .hero-content-right {
    flex: 1;
    min-width: 300px;
    text-align: center;
  }
  
  .hero-image {
    max-width: 100%;
    height: auto;
    border-radius: 12px;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
  }
  
  /* Main Headline */
  .hero-title {
    font-size: 3.5rem;
    margin-bottom: 20px;
    font-weight: 700;
  }
  
  .hero-title .highlight {
    color: #ff6347;
  }
  
  /* Subtitle */
  .hero-subtitle {
    font-size: 1.2rem;
    margin-bottom: 30px;
    font-weight: 300;
  }
  
  /* Buttons */
  .hero-buttons {
    margin-bottom: 30px;
  }
  
  .btn {
    padding: 12px 24px;
    font-size: 1rem;
    margin: 10px;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    font-weight: bold;
    transition: all 0.3s ease;
  }
  
  .primary {
    background-color: #ff6b81;
    color: white;
  }
  
  .primary:hover {
    background-color: #ff4757;
  }
  
  .secondary {
    background-color: #48c9b0;
    color: white;
  }
  
  .secondary:hover {
    background-color: #1e8547;
  }
  
  /* Install Box */
  .install-box {
    background-color: #2d3748;
    padding: 15px;
    margin: 30px 0;
    border-radius: 8px;
    text-align: left;
  }
  
  .install-box code {
    color: #ff6b81;
    font-family: 'Courier New', monospace;
  }
  
  /* Stats */
  .stats {
    font-size: 1rem;
    margin-bottom: 30px;
    color: #ffffff;
    font-weight: bold;
    text-align: center;
  }
  
  /* Features Section */
  .features {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 20px;
    margin-bottom: 30px;
  }
  
  .feature-card {
    background-color: #ffffff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    font-size: 1.2rem;
    font-weight: 600;
    color: #2d3748;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    text-align: center;
  }
  
  .feature-card:hover {
    transform: scale(1.05);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
  }
  
  /* Code Preview */
  .code-preview {
    background-color: #2d3748;
    color: #f1f1f1;
    padding: 20px;
    border-radius: 10px;
    margin-bottom: 30px;
    font-family: 'Courier New', monospace;
    overflow-x: auto;
  }
  
  /* Supported Frameworks */
  .frameworks {
    margin-bottom: 30px;
    text-align: center;
  }
  
  .frameworks .logo-list {
    font-size: 1.2rem;
    display: flex;
    justify-content: center;
    gap: 20px;
    flex-wrap: wrap;
  }
  
  .frameworks .logo-list span {
    background-color: #444;
    padding: 8px 12px;
    border-radius: 5px;
    color: white;
  }
  
  /* Community Line */
  .community {
    font-size: 1.2rem;
    color: #ff6b81;
    text-align: center;
  }
  
  /* Responsive Adjustments */
  @media (max-width: 768px) {
    .hero-title {
      font-size: 2.5rem;
    }
  
    .hero-grid {
      flex-direction: column;
      text-align: center;
    }
  
    .hero-content-left,
    .hero-content-right {
      text-align: center;
    }
  }
  